<template>
  <div>
    <div class="score">当前得分为：<span>0</span></div>
    <div class="bcbk-box" @click.self="gameover">
      <!--    P的目的是获取黑块的宽高-->
      <p class="bcbk-p"></p>
    </div>
    <div class="model-btn">
      <button @click="modelOne">街机模式</button>
      <button @click="modelTwo">经典模式</button>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import "../assets/js/mode";
import {
  modelOne,
  modelTwo,
  creatDiv,
  Random,
  start,
  sumScore,
  cover,
  speedAdd,
  move,
  gameover
} from "../assets/js/mode";
// import "../assets/js/click";
export default {
  beforeCreate() {
    document.querySelector("body").setAttribute("style", "background:#FFF;");
  },
  data() {
    //这里存放数据
    return {};
  },
  methods: {
    modelOne() {
      modelOne();
    },
    modelTwo() {
      modelTwo();
    },
    gameover() {
      gameover();
    }
  }
};
</script>
<style scoped>
@import "../assets/css/bcbk.css";
</style>


