//0
//创建div
function creatDiv () {
    $("<div class='hk'></div>").css({
        left: Random() + "px",
        top: -$(".bcbk-p")[0].offsetHeight,
        // transition: "all "+10000-parseInt(score/100)+"ms"
    }).addClass("bcbk-wz").appendTo($(".bcbk-box"));

}

//黑块水平方向位置随机
function Random () {
    return (parseInt(Math.random() * 4) * ($(".bcbk-p")[0].offsetWidth)) - 0.75;
}


//点击黑块以外部分，游戏结束
function gameover () {
    $(".bcbk-box").click(function (e) {
        $(".bcbk-box").off("click", "div");
        // alert(123
        // event.stopPropagation();
        if (e.target == e.currentTarget) {
            alert("游戏结束,您的分数为：" + score);
            start(cover());
        }
    });
}


//初始化,清空box里的黑块
function start () {
    $(".bcbk-box").children(".bcbk-wz").remove();
    $("span").text(0);
    score = 0;
    speedLevel = 0;
    // clearInterval(timer);
    //清除页面所有定时器
    for (let i = 0; i < 10; i++) {
        clearInterval(i);
    }
}
//计算分数
function sumScore () {
    return score = $("span").text();
}
//回调函数，让按钮恢复点击功能
function cover () {
    var btn = document.querySelectorAll("button");
    for (let i = 0; i < btn.length; i++) {
        btn[i].disabled = false;
    }
}
//加速
function speedAdd () {
    if (speedLevel <= 5) {
        if (score % 100 == 0) {
            timer2 = setInterval(move, 30 - speedLevel * 5);
            timer2;
            speedLevel++;
        }
    } else {
        return;
    }
}
var flag;


//2
function modelTwo (callback) {//经典模式
    flag = true;
    start()
    if (timer) {
        clearInterval(timer);
    }
    for (let i = 0; i < 6; i++) {
        $("<div class='hk'></div>").css({
            left: Random() + "px",
            top: $(".bcbk-p")[0].offsetHeight * i + 1.5,
            // transition: "all "+10000-parseInt(score/100)+"ms"
        }).addClass("bcbk-wz").appendTo($(".bcbk-box"));
    }
    $("button").prop("disabled", "true");
    click();
}

//1
var timer = null;
var timer2 = null;
var score = 0;
var speedLevel = 0;
//开始游戏函数,经典模式
function modelOne (callback) {//接机模式
    flag = false;
    start()
    if (timer) {
        clearInterval(timer);
    }
    //游戏开始先创建一个黑块
    creatDiv();
    timer = setInterval(move, 10);
    $("button").prop("disabled", "true");
    click();
}

//黑块移动
function move () {
    var wz = document.querySelectorAll(".bcbk-wz");
    for (let i = 0; i < wz.length; i++) {
        wz[i].style.top = wz[i].offsetTop + 1 + "px";
    }
    //判断黑块距离，为0时再创建一个
    if (parseInt($(".bcbk-wz:last").css("top")) >= 0) {//大于0时会有空隙
        creatDiv();
    }

    //判断最底部

    if (parseInt($(".bcbk-wz:first").css("top")) > parseInt($(".bcbk-box")[0].offsetHeight)) {
        if ($(".bcbk-wz:first").hasClass("hk")) {
            alert("游戏结束,您的分数为：" + score);
            start(cover());
        } else {
            $(".bcbk-wz:first").remove();
        }
    }
}
function click () {
    //模式判断，falg为true 经典模式
    if (flag == false) {
        $(".bcbk-box").off("click", "div");
        // $(".box").off("click","div:first");
        $(".bcbk-box").on("click", "div", function (event) { //接机模式
            event.stopPropagation();
            //点击时判断黑块颜色是否改变
            if ($(this).hasClass("hk")) {
                score += 10;
                $("span").text(score);
            }
            //黑块点击后改变背景色,清除hk类
            $(this).removeClass("hk").css("background", "pink");
            $(this).off("click");
            speedAdd();
            // console.log(flag)
        })
    } else {
        $(".bcbk-box").off("click", "div");
        $(".bcbk-box").on("click", "div", function (event) { //经典模式
            event.stopPropagation();
            if ($(this).hasClass("hk")) {
                score += 10;
                $("span").text(score);
            }
            $(this).removeClass("hk").css("background", "pink");
            $(this).off("click");
            creatDiv();
            var wz = document.querySelectorAll(".bcbk-wz");
            for (let i = 0; i < wz.length; i++) {
                wz[i].style.top = wz[i].offsetTop + parseInt($(".bcbk-p").outerHeight()) + "px";
            }
            // console.log(flag)
        });
    }
}
export {
    modelOne,
    modelTwo,
    creatDiv,
    Random,
    start,
    sumScore,
    cover,
    speedAdd,
    move,
    gameover
}











